import * as React from 'react';
import Box from '@mui/material/Box';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import { useNavigate } from 'react-router';

export default function BusinessCaseCard(props) {
  const navigate = useNavigate();

  const handleSeeDetailClick = ()=>{
    navigate('/details/'+props.data.name, {state:{data:props.data}});
  }

  console.log(props);
  return (
    <Card sx={{ minWidth: 275 }}>
      <CardContent>
        <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
          Affaire No {props.number + 1}
        </Typography>
        <Typography variant="h5" component="div">
          {props.data.name}
        </Typography>
        <Typography sx={{ mb: 1.5 }} color="text.secondary">
          
        </Typography>
        <Typography variant="body2">
          Dure en jour : {props.data.duration}.
          <br />
          {/* CA total : {props.data.gross.amount} */}
          <br />
          Bnfices potentiels : {props.data.totalProfit}
          <br />
          Marge : {props.data.margin} %
        </Typography>
      </CardContent>
      <CardActions>
        <Button size="small" onClick={handleSeeDetailClick}>Learn More</Button>
      </CardActions>
    </Card>
  );
}
